import { Component } from '@angular/core';
import { IValueWithLabel } from '@helix/platform/utils';
import { ActiveModalRef } from '@bmc-ux/adapt-angular';

@Component({
  selector: 'rx-read-only-fields-modal',
  templateUrl: './read-only-fields-modal.component.html',
  styleUrls: ['./read-only-fields-modal.component.scss']
})
export class ReadOnlyFieldsModalComponent {
  fields: IValueWithLabel[] = this.activeModalRef.getData().fields;

  constructor(private activeModalRef: ActiveModalRef) {}

  close(): void {
    this.activeModalRef.close();
  }
}
